.skills-container {
    height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background-color: #71677c;
}

.container-bar {
    border: 2px solid #fff;
    border-radius: 30px;
    display: flex;
    flex: 1;
    margin-top: .3rem;
}

.skill-container {
    display: flex;
    flex-direction: column;
    width: 98%;
    height: 8vh;
}

.filling-bar {
    background-color: #fff;
    border-radius: 30px 0 0 30px;
}